	module.exports = {"title":"about andy coenen","description":"Im a programmer, inventor, artist and musician working and living in San Francisco. In 2010 I co-founded The M Machine, an acclaimed electronic music and media project, and built some really cool things. In 2015 I switched my professional focus to","readTime":"1 min read","words":185,"body":"<p>Im a programmer, inventor, artist and musician working and living in San Francisco. In 2010 I co-founded <a href=\"https://www.the-m-machine.com\" target=\"_blank\" rel=\"noopener noreferrer\">The M Machine</a>, an acclaimed electronic music and media project, and built some <a href=\"/projects/\" target=\"_blank\" rel=\"noopener noreferrer\">really</a> <a href=\"/projects/\" target=\"_blank\" rel=\"noopener noreferrer\">cool</a> <a href=\"/projects/\" target=\"_blank\" rel=\"noopener noreferrer\">things</a>. In 2015 I switched my professional focus to software engineering and am currently working as a software engineer at <a href=\"https://research.google.com/teams/brain/healthcare/\" target=\"_blank\" rel=\"noopener noreferrer\">Google Brain</a>. In a past life I was a neuroscientist, and Im still deeply fascinated by biosciences, perception, and artificial intelligence.</p>\n<p>Im passionate about bleeding edge technology, and am especially interested in human-machine interaction, programming language design, and creative coding. I believe deeply in building the tools of the future and am inspired by turning imagination into reality.</p>\n<p><strong>I love to work with:</strong></p>\n<p> node.js | python <br />\n react | redux | mobx <br />\n mongodb | postgresql | elasticsearch <br />\n atom | electron | typescript <br /></p>\n<p><strong>I love to play with:</strong></p>\n<p> unity | oculus | three.js <br />\n juce | c++ <br />\n ableton | logic <br />\n touch designer | max/msp <br />\n osc | led | arduino <br />\n machine learning | neural networks | AI <br /></p>\n<p><br><br></p>\n<p><small>This site built with <a href=\"https://github.com/gatsbyjs/gatsby\" target=\"_blank\" rel=\"noopener noreferrer\">gatsby.js</a> and modern web technologies | </small>\n<span class=\"markdown-no-underline\"><a href=\"https://github.com/cannoneyed/cannoneyed.github.io\" target=\"_blank\" rel=\"noopener noreferrer\"><img src=\"../../icons/github.svg\" class=\"markdown-icon\"> </a></span></p>\n"}

