	module.exports = {"title":"OSC and the future of signaling","date":"01/02/2015","description":"A new signaling protocol for new means of expression","thumbnail":"max-ableton.jpg","readTime":"3 min read","words":579,"body":"<figure class=\"full-width\"><img src=\"./max-ableton.jpg\" alt=\"\"></figure>\n<p>The most consistent challenge Ive encountered in my decade of exploring interactive media has been how to get information from point A to point B in a way that fosters creativity. Turning a knob and seeing or hearing a program react is amazing, but true expressivity comes from a careful attention to the physics of the interaction. Like playing a finely crafted instrument, creativity and expressivity is dependent on the nuance, subtlety, and reactivity provided by great tool design.</p>\n<hr>\n<p>Media artists have been empowered by a relatively recent explosion of creative technologies - Ableton Live revolutionized digital audio performance and, partnered with programs like Max/MSP, Touch Designer, and Resolume Avenue, can serve as a hub for a complete multimedia experience. But at its core, the signaling architecture most commonly used by these programs remains unchanged since its inception in the 80s.</p>\n<p>MIDI is a remarkably robust technology - standardized in 1983, its current ubiquity is testament to that fact. But with everyday computing power many orders of magnitude greater today and an almost limitless toolkit available to creative technologists, MIDIs age is showing more and more. HD video sequencing, realtime 3D composition and rendering, drone control, robotic interfacing, augmented reality, and anything imaginable can be controlled from pretty much anything But if you want to run these things from a sequencer or most hardware controllers youll still have to translate from a MIDI signal - which is limited to 7 bits (0-127) of data and has no semantic metadata.</p>\n<p>Enter OSC - a protocol first outlined by the CNMAT at UC Berkeley. OSC allows transmission of URL-encoded data at high data and time resolution over UDP (or TCP, with a bit of work). This means that any part of any system can be given a lexical OSC address to receive signals from anywhere else without complex encoding schemes or limits on data resolution, internally or over a network. For example, triggering the first video clip of the first layer in Resolume Avenue is as simple as sending the message <code>/layer1/clip1/connect 1</code> to whatever port Resolume is listening to, rather than a numbered MIDI mapping.</p>\n<figure>\n    <img src=\"./touch-osc.png\" alt=\"\">\n    <figcaption><a href=\"http://hexler.net/software/touchosc\" title=\"TouchOSC Interface enabling iPad control\">TouchOSC Interface enabling iPad control</a>.</figcaption>\n</figure>\n<p>Its in complex sets of high-resolution data where OSC can really shine - Large banks of lights, incredibly precise inverse kinematic controls for robotics, 3D generative content, or data from human-interfacing devices can all be routed and parsed incredibly quickly and easily - The expressivity and depth and breadth of control that creativity requires is much more readily achieved.</p>\n<p>In order for OSC to achieve mainstream acceptance, a major change in the toolkits of creative multimedia artists is required. Ableton Live is currently the de facto standard for central dispatching of many multimedia projects, yet currently has no packaged support for OSC. Theres a huge hole in the marketplace waiting to be filled by whatevers up to the task.</p>\n<hr>\n<p>One of my first big programming projects was a simple OSC audio unit plugin that I worked on before starting all of the precourse work required for Hack Reactor. I used it to great effect to design and produce The M Machines video content for their recent tour with The Glitch Mob, to great effect. I was able to control video in Resolume at sub-frame precision from the Logic timeline, and instantiate and modify video effects and real-time generated Quartz Composer content all from the DAW. Its been an exciting project, and I look forward to releasing a finished version soon.</p>\n"}

