	module.exports = {"title":"Console injector","date":"01/15/2015","description":"A chrome extension for quickly injecting javascript libraries from the console","readTime":"2 min read","words":332,"body":"<p>Last Thursday night we were given the opportunity to participate in a two-hour hackathon here at Hack Reactor, and when we were presented with the prompt of making a chrome extension, an idea Id been thinking about in prior weeks immediately sprung to mind. Wouldnt it be great to be able to quickly inject javascript libraries right from the console?</p>\n<p>My partner during the D3 sprint (<a href=\"https://github.com/mmconrad\" title=\"github\" target=\"_blank\" rel=\"noopener noreferrer\">Matt Conrad</a>) and I had talked about how cool this would be when we were initially exploring the library. Instead of creating a custom demo and manually installing the libraries, it would be very handy to quickly inject a library of choice into any page right from the console and get coding.</p>\n<p>Enter <a href=\"https://github.com/cannoneyed/console-injector\" target=\"_blank\" rel=\"noopener noreferrer\">Console Injector</a>! The chrome extension creates a console.inject method that can be called to quickly inject any javascript library onto a given page.</p>\n<pre><code><span class=\"hljs-built_in\">console</span>.inject(<span class=\"hljs-string\">'jquery'</span>)\n</code></pre>\n<p>The method searches <a href=\"https://cdnjs.com/\" target=\"_blank\" rel=\"noopener noreferrer\">cdnjs</a> for javascript libraries matching a given input string, and inserts the latest corresponding script hosted at <a href=\"http://cdnjs.cloudflare.com\" target=\"_blank\" rel=\"noopener noreferrer\">cdnjs.cloudflare.com</a>. The magic lies in the following snippet of code, which injects an injector script into the head of the current document:</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> element = <span class=\"hljs-built_in\">document</span>.createElement(<span class=\"hljs-string\">'script'</span>);\nelement.innerHTML = <span class=\"hljs-string\">'console.inject = '</span> + injectFunction.toString();\n<span class=\"hljs-built_in\">document</span>.head.appendChild(element);\n</code></pre>\n<p>Since the javascript executed in the chrome extension doesnt have access to any variables in the current page, in order to modify the console object (which is just another object) we must first append a script to the DOM, which then modifies the console object indirectly. This bit of redirection allows us to inject an injector script onto the page, which in turn generates more script tags with a source attribute corresponding to the queried library.</p>\n<hr>\n<p>Console Injector was a great project to learn about Chrome extensions and bring something to life in a short amount of time. Id like to give a big thanks to all of my team members: <a href=\"http://www.github.com/wesleysmyth\" target=\"_blank\" rel=\"noopener noreferrer\">Wesley Smyth</a>, <a href=\"http://www.github.com/kranrao\" target=\"_blank\" rel=\"noopener noreferrer\">Kiran Rao</a>, <a href=\"http://www.github.com/henryw4k\" target=\"_blank\" rel=\"noopener noreferrer\">Henry Wong</a>, and <a href=\"http://www.github.com/mmconrad\" target=\"_blank\" rel=\"noopener noreferrer\">Matt Conrad</a>.</p>\n<p>Console Injector is <a href=\"https://github.com/cannoneyed/console-injector\" target=\"_blank\" rel=\"noopener noreferrer\">open source</a> and available to download on the <a href=\"https://chrome.google.com/webstore/detail/abdfbnapkafgcheofcijaieahcbjnpkd\" target=\"_blank\" rel=\"noopener noreferrer\">Google Chrome Web Store</a>.</p>\n"}

