	module.exports = {"title":"GeoJSON Queries in MongoDB","date":"02/05/2015","description":"Location based querying across dimensions","readTime":"2 min read","words":372,"body":"<p>MongoDB has gained a lot of momentum over the last few years by presenting itself as a more versatile and free form database alternative to the highly structured, schema-based standards like mySQL. However mongo offers a number of incredibly powerful query abilities that rely on setting up a more rigid set of indices, including robust geographic search capabilities based on geoJSON.</p>\n<p>GeoJSON is a format for encoding a variety of geographic features in a specific way.</p>\n<pre><code class=\"language-javascript\">{\n  <span class=\"hljs-string\">\"type\"</span>: <span class=\"hljs-string\">\"Feature\"</span>,\n  <span class=\"hljs-string\">\"geometry\"</span>: {\n    <span class=\"hljs-string\">\"type\"</span>: <span class=\"hljs-string\">\"Point\"</span>,\n    <span class=\"hljs-string\">\"coordinates\"</span>: [<span class=\"hljs-number\">-122.409046</span>, <span class=\"hljs-number\">37.783748</span>]\n  },\n  <span class=\"hljs-string\">\"properties\"</span>: {\n    <span class=\"hljs-string\">\"name\"</span>: <span class=\"hljs-string\">\"Hack Reactor\"</span>\n  }\n}\n</code></pre>\n<p>The first thing to note is that geoJSON encodes the coordinates of a feature as an array pair of [longitude, latitude], which is the opposite of many popular georgraphic services (like the Google Maps API) which returns latitude / longitude pairs. The second point of interest is that a geoJSON object can contain a variety of geometries. This example represents a single point, but any polygon or shape can be represented.</p>\n<p>In order to query mongoDB based on geoJSON objects, the data in the database must be indexed geographically. This allows mongo to look up geographic data by a geographic query very efficiently. Its as simple as setting up an index, which can be done using mongoose:</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> mongoose = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'mongoose'</span>);\n\n<span class=\"hljs-keyword\">var</span> LocationSchema = <span class=\"hljs-keyword\">new</span> mongoose.Schema({\n  <span class=\"hljs-attr\">location</span> : {\n    <span class=\"hljs-attr\">type</span>: {\n      <span class=\"hljs-attr\">type</span>: <span class=\"hljs-built_in\">String</span>,\n      <span class=\"hljs-attr\">default</span>: <span class=\"hljs-string\">'Point'</span>\n    },\n    <span class=\"hljs-attr\">coordinates</span>: [<span class=\"hljs-built_in\">Number</span>]\n  }\n});\n\nLocationSchema.index({ <span class=\"hljs-attr\">location</span> : <span class=\"hljs-string\">'2dsphere'</span> });\n\nLocation = mongoose.model(<span class=\"hljs-string\">'Location'</span>, LocationSchema);\n</code></pre>\n<p>This sets up a simple schema of a location object encoded in geoJSON based format. By indexing the location object to type 2dsphere, mongo can very efficiently query this set of data based on passed in geoJSON queries.</p>\n<pre><code class=\"language-javascript\">query.location = {\n  <span class=\"hljs-attr\">$near</span> : {\n    <span class=\"hljs-attr\">$geometry</span> : {\n      <span class=\"hljs-attr\">type</span> : <span class=\"hljs-string\">\"Point\"</span>,\n      <span class=\"hljs-attr\">coordinates</span> : [<span class=\"hljs-number\">-122.409046</span>, <span class=\"hljs-number\">37.783748</span>]\n    },\n    <span class=\"hljs-attr\">$maxDistance</span> : <span class=\"hljs-number\">1000</span>\n  }\n}\n\nLocation.find(query);\n</code></pre>\n<p>One of the most powerful features that the geoJSON querying format allows for is using complex geometries to query the database. The example above simply searches for all points within a radius of 1000 meters, but by using a complex polygonal geometry as the query geometry, mongo can search within any imaginable bounding object, for instance all of the points within a few blocks of a given route.</p>\n"}

