	module.exports = {"title":"The M Machine - LED Light Wall","date":"01/01/2011","description":"custom built LED light wall touring visuals","thumbnail":"thumbnail.jpg","readTime":"4 min read","words":734,"body":"<figure class=\"full-width\"><img src=\"./thumbnail.jpg\" alt=\"\"></figure>\n<p>In 2010, Eric Luttrell, Ben Swardlick founded The M Machine, and decided to embark on a remarkable journey that would take us around the world.</p>\n<p>At the time, I was extremely excited about synchronized visuals (think Daft Punks 2006 Pyramid Coachella show) and was doing a ton of experimentation with driving LEDs with Arduino and Max / MSP. What started out as a set of experiments evolved into what became the centerpiece for our bands early years, a giant LED M stagepiece that we performed with across the country.</p>\n<p><video src=\"./m-light-wall-03.webm\" width=\"100%\" autoplay loop class=\"glare-thumbnail\"></video></p>\n<p>The real-life M Machine was over 18&quot; wide, consisting of 36 18&quot;x18&quot; acrylic light-guiding panels with RGB LED illuminating the interior using FTIR. I ordered the acrylic panels custom from a supplier in China, and was shocked to learn that they weighed over 1000 pounds and would have to be delivered by cargo container. Theyd prove to be incredibly durable and withstand 100s of shows and 1000s of miles of travel.</p>\n<figure class=\"full-width\"><img src=\"./m-wall-warehouse.jpg\" alt=\"The M Machine set up in our dogpatch warehouse\"><figcaption>The M Machine set up in our dogpatch warehouse</figcaption></figure>\n<p>Each panel was driven by an RGB DMX controller, with current actually delivered over USB (A super-cheap, durable 4-wire conductor). The M frame attached to the M was actually aluminum gutter from Lowes, cut to shape, and outfitted with 20 additional channels of LED ribbon. The entire stagepiece consumed about 12A of power at full white brightness.</p>\n<figure class=\"full-width\"><img src=\"./m-driver-boxes.jpg\" alt=\"The LED driver boxes, located in the truss - DMX signal cable, DC over 1/4&quot;, power to panels over USB.\"><figcaption>The LED driver boxes, located in the truss - DMX signal cable, DC over 1/4&quot;, power to panels over USB.</figcaption></figure>\n<p><video src=\"./m-light-wall-02.webm\" width=\"100%\" autoplay loop class=\"glare-thumbnail\"></video></p>\n<p>I sequenced the visuals using a suite of custom software that I built for the task - I used a touchOSC interface that I built on my iPad which communicated via OSC with a Max For Live plugin. Basically, the touchOSC interface had a timeline of one bar divided into 48 keyframes on a timeline (allowing for 16th notes or 12th note triplets). Each keyframe had an array of buttons corresponding to each panel / outline piece. For any given panel / outline, you picked a start color and brightness, end color and brightness, and duration, then assigned it to a panel at a given position on the timeline. I used 16 colors with 8 brightness steps and encoded them into each of the 16 divisions of the 7-bit MIDI value.</p>\n<pre><code>MIDI velocity |<span class=\"hljs-number\">00</span> <span class=\"hljs-number\">01</span> <span class=\"hljs-number\">02</span> <span class=\"hljs-number\">03</span> <span class=\"hljs-number\">04</span> <span class=\"hljs-number\">05</span> <span class=\"hljs-number\">06</span> <span class=\"hljs-number\">07</span>|<span class=\"hljs-number\">08</span> <span class=\"hljs-number\">09</span> <span class=\"hljs-number\">10</span> <span class=\"hljs-number\">11</span> <span class=\"hljs-number\">12</span> <span class=\"hljs-number\">13</span> <span class=\"hljs-number\">14</span> <span class=\"hljs-number\">15</span>|...\ncolor         |red                    |blue                   |...\nbrightness    |<span class=\"hljs-number\">1</span>  <span class=\"hljs-number\">2</span>  <span class=\"hljs-number\">3</span>  <span class=\"hljs-number\">4</span>  <span class=\"hljs-number\">5</span>  <span class=\"hljs-number\">6</span>  <span class=\"hljs-number\">7</span>  <span class=\"hljs-number\">8</span> |<span class=\"hljs-number\">1</span>  <span class=\"hljs-number\">2</span>  <span class=\"hljs-number\">3</span>  <span class=\"hljs-number\">4</span>  <span class=\"hljs-number\">5</span>  <span class=\"hljs-number\">6</span>  <span class=\"hljs-number\">7</span>  <span class=\"hljs-number\">8</span> |...\n</code></pre>\n<figure class=\"full-width\"><img src=\"./m-touch-osc.jpg\" alt=\"custom touchOSC interface for sequencing patterns\"><figcaption>custom touchOSC interface for sequencing patterns</figcaption></figure>\n<p>These three parameters were encoded as three MIDI notes, and touchOSC would print these MIDI notes into clips in Ableton using Max for Live. Ableton would play back the encoded MIDI signals, outputting them to another Max patch, which would decode the MIDI triplet and output DMX signal to drive the LED wall.</p>\n<figure class=\"full-width\"><img src=\"./m-max-patch.jpg\" alt=\"the wonderful world of Max/MSP\"><figcaption>the wonderful world of Max/MSP</figcaption></figure>\n<p><video src=\"./m-light-wall-04.webm\" width=\"100%\" autoplay loop class=\"glare-thumbnail\"></video></p>\n<p>Working bar-by-bar, and tile-by-tile, I sequenced out visual representations of our music - thousands of hours of work at what I can only describe as an extremely esoteric skill. I constantly learned new tricks, and continually iterated on the tooling to improve the developer experience - adding a mirror-mode, allowing Ableton to send data back to the iPad for editing, and creating a whole suite of macro tools for patterns that I used frequently.</p>\n<figure class=\"full-width\"><img src=\"./m-sequencer-ableton.jpg\" alt=\"a typical ableton session with seqenced LED visuals encoded as MIDI\"><figcaption>a typical ableton session with seqenced LED visuals encoded as MIDI</figcaption></figure>\n<p>Whats perhaps most interesting about The real-life M Machine is how richly varied the expression of visual metaphor language it provided was - I sequenced visuals for more than 30 songs, and never really repeated myself too similarly - each track had its own color palette, and each sound had its own visual representation. Also striking is how important ones innate feeling of physics is to creating audio-visual synchronization - the attack or decay of a light (how quickly it turns on or off), and how well it matches that of the sound it represents, quickly transforms the light into something directly tied into the sound, as if its the thing making the sound or vice versa.</p>\n<p>Im incredibly proud of my homemade M Machine, and truly loved performing with it. Its later video-only incarnations, while infinitely more expressive and creatively interesting, never matched this project in terms of ambition and pure, striking power.</p>\n<p><video src=\"./m-light-wall-01.webm\" width=\"100%\" autoplay loop class=\"glare-thumbnail\"></video></p>\n<h4 id=\"links\"><a class=\"header-anchor\" href=\"#links\" aria-hidden=\"true\" target=\"_blank\" rel=\"noopener noreferrer\"></a> links</h4>\n<p><a href=\"https://www.youtube.com/watch?v=vdAZHYFNbWU\" target=\"_blank\" rel=\"noopener noreferrer\">a video explanation of sequencing process</a><br/>\n<a href=\"http://www.wired.com/2012/04/m-machine-artist-collective/\" target=\"_blank\" rel=\"noopener noreferrer\">feature in WIRED</a><br/>\n<a href=\"http://iq.intel.com/concerts-2-0-the-m-machine-revamps-the-visual-experience/\" target=\"_blank\" rel=\"noopener noreferrer\">feature in Intel iQ</a><br/></p>\n"}

