	module.exports = {"title":"holograf","date":"01/01/2015","description":"3D algorithm and program flow visualizer","thumbnail":"thumbnail.jpg","readTime":"2 min read","words":218,"body":"<figure class=\"full-width\"><img src=\"./thumbnail.jpg\" alt=\"\"></figure>\n<p>holograf was my thesis project at <a href=\"http://www.hackreactor.com/\" target=\"_blank\" rel=\"noopener noreferrer\">Hack Reactor</a>, which I built with an incredible team (<a href=\"https://github.com/lucaswadedavis\" target=\"_blank\" rel=\"noopener noreferrer\">Luke Davis</a>, <a href=\"https://github.com/conrad\" target=\"_blank\" rel=\"noopener noreferrer\">Matt Conrad</a> and <a href=\"https://github.com/ryanjlee\" target=\"_blank\" rel=\"noopener noreferrer\">Ryan Lee</a>). It began as a question that Luke and I would often toss around while talking shop - what if you could write a program just like you would work with a physical machine?</p>\n<p>holograf is a 3D algorithm and program flow visualizer that traces every value throughout a programs execution and allows the user to explore the execution in 3D using three.js. In essence, holograf is a <strong>re</strong> compiler, tracking the programs execution by injecting watchpoints at every point of the program and tracking its execution. Heres how it works:</p>\n<ul>\n<li>convert the program into an abstract syntax tree (AST) using <a href=\"http://esprima.org/\" target=\"_blank\" rel=\"noopener noreferrer\"><code>esprima</code></a></li>\n<li>use a recursive descent parser to traverse the AST, injecting new nodes into the tree that will track the execution of each line as it runs (recording every detail of the programs exection including variable values, scope, and line numbers)</li>\n<li>regenerate code from the modified AST and execute it, generating a data structure representing the execution timeline and captured information of the programs execution</li>\n<li>generate a 3D representation of the programs execution using a range of visual representations generated by three.js</li>\n</ul>\n<hr>\n<figure>\n    <video src=\"./holograf_demo.webm\" width=\"100%\" autoplay loop class=\"holograf-thumbnail\"></video>\n    <figcaption><a href=\"http://hologragi.io\" title=\"holograf visualization of a simple fibonacci algorithm\">holograf visualization of a simple fibonacci algorithm</a>.</figcaption>\n</figure>\n<p><a href=\"https://github.com/cannoneyed/holograf\" target=\"_blank\" rel=\"noopener noreferrer\">&lt;source /&gt;</a></p>\n<h4 id=\"links\"><a class=\"header-anchor\" href=\"#links\" aria-hidden=\"true\" target=\"_blank\" rel=\"noopener noreferrer\"></a> links</h4>\n<p><a href=\"http://www.hackreactor.com/blog/students-create-3d-visualizations-to-map-debug-code\" target=\"_blank\" rel=\"noopener noreferrer\">Hack Reactor Blog</a><br /></p>\n"}

